import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
            hidden: false,
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                

                {/* <a style={{ display: 'block', marginTop: 40, marginBottom: 20, color: '#108ee9' }}
                            onClick={(e) => {
                                e.preventDefault();
                                this.setState({
                                hidden: !this.state.hidden,
                                });
                            }}
                    >
                    Click to show/hide tab-bar
                    </a> */}
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                    hidden={this.state.hidden}
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-Homehomepagemenu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icon-Homehomepagemenu1'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-gouwuche'></i>                         
                        }
                        selectedIcon={
                            <i className='iconfont icon-gouwuche1'></i>                           
                        }
                        title="购物车"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    ></TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan1'></i>
                        }
                      
                        selectedIcon={
                            <i className='iconfont icon-dingdan'></i>                        
                        }
                        title="订单列表"
                        key="list"
                        // selected={ this.state.selectedTab === 'greenTab' }
                        // onPress={ () => {
                        //     this.setState({
                        //         selectedTab: 'greenTab',
                        //     });
                        // } }
                        selected={ pathname === '/list' }
                        onPress={ () => {
                            this.props.history.push('/list')
                        } }
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont icon-yonghu'></i> }
                        selectedIcon={
                            <i className='iconfont icon-yonghu1'></i>                           
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/my' }
                        onPress={ () => {
                            this.props.history.push('/my')
                        } }
                        // selected={ this.state.selectedTab === 'yellowTab' }
                        // onPress={ () => {
                        //     this.setState({
                        //         selectedTab: 'yellowTab',
                        //     });
                        // } }
                    >                       
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);