import React from 'react'
import './listone.css'
import { NavBar, Icon } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

const Listone = (props) => {
    return (
        <div className="con">
            <div className="part">
                <div className="im">
                    <img src={require("./img/list-one.webp")} alt=""/>
                </div>
                <div className="parttwo">￥40</div>
                <div className="partone">HZ大容量ins耐高温玻璃饮料杯早餐牛奶杯果汁带盖吸管星星水杯</div>
                <div className="partthree" onClick={()=>props.history.go(-1)}>               
                    <Icon type="left" size="lg" color="white"/>                   
                </div>
            </div>
            <div className="wei">
            <ul>
                <li className="wei-t1">
                    <i className='iconfont icon-dianpu'></i>
                    <p>店铺</p>
                </li> 
                <li className="wei-t1"><i className='iconfont icon-kefu'></i><p>客服</p></li>  
                <li className="wei-t1"><i className='iconfont icon-shoucang'></i><p>收藏</p></li>    
                <li className="wei-b1">加入购物车</li>
                <li className="wei-b2">立即购买</li>
            </ul>
            </div>
            
        </div>
    )
}

export default withRouter(Listone);